.root {
  border-radius: 7px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.noBorder {
  border: none;
}

.company {
  border: 1px solid gainsboro;
  border-radius: 3px;
  width: 120px;
  padding: 7px 0 10px 0;
  text-align: center;
  background-color: #f8f8f8;
  margin: 0 2px;
}

.companyMultiParent {
  border: 1px solid blue;
}

.name {
  font-weight: bold;
}

.subsidiaries {
  display: flex;
}

.border {
  width: 100%;
  display: flex;
}

.borderOn {
  width: 100%;
  border-bottom: 1px solid #888888;  
}

.borderOff {
  width: 100%;
}
